@import '../sass/_variables.scss';
@import '../sass/_sprite-icons.scss';
@import '../sass/_sprite-menu.scss';
@import '../sass/_mixins&includes.scss';
/* header style */
/* top header style */
.top-header {
  @extend .clearfix;
  // padding: 27px 0 22px;

  .container {
    padding-top: 28px;
    padding-bottom: 23px;
    position: relative;

    &:before {
      content: "";
      width: 98%;
      height: 1px;
      background: #e2e2e2;
      position: absolute;
      right: 0;
      left: 10px;
      bottom: 0;
    }
  }

  // border-bottom: #E1E1E1 1px solid;
}

/* logo style */
.logo-holder {
  float: left;
  font-size: 0;
  margin: 0 0 0 -2px;
  li {
    @extend .inline-b;
    font-size: 14px;
    margin: 0 0 0 2px;
    &:last-child {
      margin: 34px 0 0 2px;
    }

  }
}
.logo {
  background: url('../img/logo-header.png') no-repeat;
  width: 142px;
  height: 76px;
  display: inline-block;
  a {
    width: 142px;
    height: 76px;
    display: block;
    text-indent: -99999px;
  }
  &:hover {
    opacity: 0.5;
  }
}
.slogan {
  font-size: ($sm_fs + 10px);
  font-family: 'Times New Roman', 'Times', serif;
  color: $main_color;
  display: block;
}

/* search style */
.search-holder {
  overflow: hidden;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  margin: 2px 0 0 0;
  float: right;
  position: relative;
  @extend .clearfix;
  height: 70px;
  width: 535px;
  form {
    @extend .clearfix;
  }
  &:before {
    content: "";
    width: 35px;
    height: 100px;
    background: $main_bg;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
  }
  .btn-wrap {
    float: right;
    position: absolute;
    right: 0;
    top: 0;z-index: 2;
    &:hover {
      .input-wrap {
        display: block;
      }
    }
  }
  .input-wrap {
    width: 100%;
    float: left;
    // width: 535px;
    display: none;
    &.active {
      .input {
        border-color: #ac2817;
      }
    }
    &.visible {
      display: block;
      .input.search {
        border-color: #f15d4a;
      }
    }
  }

}

.basket-holder {
  float: right;
  margin: 3px 0 0 20px;
}
.basket-count {
  color: #FFF;
  background: $red_color;
  display: block;
  text-align: center;
  height: 14px;
  width: 17px;
  line-height: 14px;
  position: absolute;
  right: 13px;
  top: 15px;
  font-size: 11px;
  font-family: 'Arial', 'Helvetica', sans-serif;
  border-radius: 4px;

}

/* bottom header style */
.bottom-header {
  @extend .clearfix;
  padding: 0 0 20px;
}

/* nav style */
.nav-holder {
  float: left;
  position: relative;
  z-index: 101;
  // width: 220px;
  .icon {
    display: table;
    float: left;
    width: 21px;
    height: 21px;
    text-align: center;
    vertical-align: middle;
    font-size: 18px;
  }
  .menu-text {
    margin: 0 0 0 20px;
  }
  .icon-wrap {
    display: table-cell;
    height: 21px;
    width: 21px;
    vertical-align: middle;
    text-align: center;
  }
  .menu-text {
    display: inline-block;
    margin: 0 0 0 19px;
    font-size: $xmd_fs;
    margin: 2px 0 0 19px;

    vertical-align: top  }
  a {
    font-size: 0;
    position: relative;
    color: $main_color;
    text-decoration: none;
    font-size: $xmd_fs;
    font-family: $helvetica-l;
    padding: 12px 15px 17px 17px;

    display: block;
    // &:active,
    // &.active {
    //   background: $dark_red_color;
    //   color: #FFF;
    //   .icon-catalog {
    //     @include sprite($icon-catalog-white);
    //   }

    //   .icon-about {
    //     @include sprite($icon-about-white);
    //   }


    //   .icon-company {
    //     @include sprite($icon-company-white);
    //   }

    //   .icon-news {
    //     @include sprite($icon-news-white);
    //   }

    //   .icon-recomendation {
    //     @include sprite($icon-recom-white);
    //   }

    //   .icon-help {
    //     @include sprite($icon-help-white);
    //   }
    // }
  }
  li {
    border-bottom: #E0E0E0 1px solid;
    position: relative;

    background: $main_bg;
    transition: all 0.5s ease 0s;
    li {
      a {
        color: #000;

      }
    }
    &:hover {
      background: $light_red_color;
      color: $main_bg;
      a {
        color: $main_bg;
      }
      .sub-menu-holder {
        display: block;
      }
      .icon-catalog {
        @include sprite($icon-catalog-white);
      }

      .icon-about {
        @include sprite($icon-about-white);
      }

      .icon-company {
        @include sprite($icon-company-white);
      }

      .icon-news {
        @include sprite($icon-news-white);
      }

      .icon-recomendation {
        @include sprite($icon-recom-white);
      }

      .icon-help {
        @include sprite($icon-help-white);
      }
      li {
        a {
          color: #000;
        }
      }
    }
    &:active {
      background: $dark_red_color;
    }

  }
}
.nav {
  @extend %menu-common;
  right: 0;
  left: 0;

  &.visible {
    @extend %menu-common-active;
  }
}
.dropped-link {
  &:before {
    content: "";
    @include sprite($icon-arrow-black);
    position: absolute;
    right: 16px;
    top: 50%;
    margin: -6px 0 0 0;
  }
  &:hover {
    &:before {
      @include sprite($icon-arrow-white);
    }
  }
}
.sub-menu-holder {
  box-shadow: -3px -4px 30px 0px rgba(0, 0, 0, 0.25);
  display: none;
  font-size: 0;
  white-space: nowrap;
  position: absolute;
  background: #fff;
  background-clip: padding-box;
  border-left: 1px solid transparent;
  // width: 100%;
  left: 100%;
  // left: 0;
  top: 0;
  // margin: 0 0 0 220px;

  // IE doesn't know what the Heck is "initial"
  // box-sizing: initial;
  // @extend .clearfix;
  &:hover {
  }

  .sub-menu-list {
    // float: left;
    display: inline-block;
    margin: 0 0 0 20px;
    width: 250px;
    li {
      width: 100%;
      line-height: 28px;


    }
    a {
      position: relative;
      color: #000;
      padding: 12px 15px 15px 23px;
      &:before {
        content: "";
        @include sprite($icon-arrow-black);
        position: absolute;
        right: 15px;
        top: 50%;
        margin: -6px 0 0 0;
      }
      &:hover {
        color: $main_bg;
        &:before {
          @include sprite($icon-arrow-white);
        }
      }
      // &:focus {
      //   color: $main_bg;
      //   &:before {
      //     @include sprite($icon-arrow-white);
      //   }
      // }
    }
    &:first-child {
      margin: 0;
    }
    &.none {
      a {
        &:before {
          content: none;
        }
      }
    }
  }

}

.menu-btn-holder {
  width: 220px;
  cursor: pointer;
  font-size: 0;
  position: relative;
  color: $main_color;
  text-decoration: none;
  font-size: $xmd_fs;
  font-family: $helvetica-l;
  padding: 13px 15px 17px 18px;
  background: $main_bg;
  display: block;
  transition: all 0.5s ease 0s;

  &:hover {
    background: #f15d4a;
    color: $main_bg;
    .icon-menu {
      @include sprite($icon-menu-white);
    }
  }
  &:focus {
    background: #ac2817;
    color: $main_bg;
    .icon-menu {
      @include sprite($icon-menu-white);
    }
  }
  &.active,
  &:active {
    background: #d4412d;
    color: $main_bg;
    .icon-menu {
      @include sprite($icon-menu-white);
    }
  }
  .menu-text {
    display: inline-block;
    margin: 0 0 0 22px;
    font-size: $xmd_fs;
  }
}

.chose-holder {
  float: right;
  position: relative;
  .chose-list {
    font-size: 0;
    font-family: $helvetica-l;
    li {
      @extend .inline-b;
      position: static;
      font-size: $xmd_fs;
      color: $main_color;
      &:first-child {
        .country-select {
          &:hover,
          &.active {
            img {
              filter: invert(100%);
            }
          }
        }
      }
      a {
        color: $main_color;
        transition: all 0.5s ease 0s;
        display: block;
        position: relative;
        &:hover {
          text-decoration: none;
        }
      }
      h3 {
        font-weight: normal;
        font-family: $helvetica-l;
      }
      .country-select {
        &.selected {
          &:hover,
          &.active {
            img {
              filter: invert(0%);
            }
          }
        }
      }
    }
  }
  .country-select {
    padding: 13px 22px 16px 22px;
    letter-spacing: 0.1px;

    span {
      margin: 0 0 0 7px;
    }
    img,span {
      @extend .inline-m;
    }
    &:hover {
      color: #fff;
      background: #f15d4a;
      img {
        // filter: invert(100%);
      }

    }
    &:focus {
      color: #fff;
      background: #ac2817;
      img {
        // filter: invert(100%);
      }

    }
    &.active,
    &:active {
      img {
        // filter: invert(100%);
      }
      color: #fff;
      background: #d4412d;

    }
    &:before {
      content: "";
      position: absolute;
      top: 50%;

      left: 24px;

      margin: -11px 0 0 0;
    }
  }
  .lang-select {
    @extend .country-select;
    padding: 12px 23px 17px 27px;
    letter-spacing: -0.3px;
    span {
      margin: 0 0 0 8px;
    }
    &:hover,
    &.active {

    }
  }
}
.country-holder {
  background: #fff;

  position: absolute;
  bottom: inherit;
  right: 0;
  width: 586px;
  box-shadow: -3px -4px 30px 0px rgba(50, 50, 50, 0.25);
  opacity: 0;
  margin: -1000px 0 0 0;
  transition: all 0.5s ease 0s;
    opacity: 0;
    // z-index: 0;
    z-index: 100;
    transition: margin .5s ease-in-out,
                opacity .5s ease-in-out;
    &.visible {
      opacity: 1;
      margin: 0;
      // z-index: 100;
    }
  &.lang {
    right: 0;
  }
  .title {
    position: relative;
    @extend .clearfix;
    padding: 41px 27px 40px 32px;
    h3 {
      font-size: $lg_fs;
      font-family: $helvetica-l;
      padding: 0;
      text-transform: none;
      &:first-letter {
        text-transform: uppercase;
      }
    }
  }
  .close {
    @include sprite($icon-close-black-lg);
    position: absolute !important;
    right: 26px;
    top: 41px;
    cursor: pointer;
    display: inline;
    transition: none !important;
    &:hover {
      @include sprite($icon-close-orange-lg);
    }
    &:focus,
    &:active {
      @include sprite($icon-close-dark-orange-lg);
    }
  }
  .country-list {
    font-size: 0;
    margin: 0 0 0 -20px;
    li {
      margin: 0 0 0 20px;
      width: 182px;
      border-top: 1px solid #e0e0e0;
      font-family: $helvetica-l;


      a {
        padding: 14px 0 15px 19px;
        font-size: 0;
        color: $main_color;
        &:hover {
          color: #FFF;
          background: $light_red_color;
        }
        &:active,
        &:focus {
          color: #fff;
          background: #d4412d;
        }

      }
      span,
      img {
        @extend .inline-b;
        font-size: $xmd_fs;
      }
      span {
        margin: 0 0 0 20px;

        letter-spacing: -0.6px;
      }
    }
  }
}
%menu-common {
  opacity: 0;
  box-shadow: -3px -4px 30px rgba(50, 50, 50, 0.25);
  position: absolute;
  top: 100%;
  z-index: 100;

  transform: translateY(20px);
  visibility: hidden;
  transition: visibility 0s .5s,  opacity .5s ease-in-out,
              transform .5s ease-in-out;
}
%menu-common-active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition-delay: 0s;
}

.country-holder {
