@media (max-width: 1024px) {
  body {
    min-width: 1000px;
  }
  .container {
    margin: 0 auto;
    padding: 0px;
    position: relative;
    width: 1000px;
    &.small {
      padding-left: 10px;
    }
  }
  .top-header .container {
       padding: 28px 4px 23px;
       &:before {
         left: 0;
         width: 100%;
       }
  }
  .footer {
    .container {
      &:before {
        left: 0;
        width: 100%;
      }
    }
  }
}