@import '../sass/_variables.scss';
@import '../sass/_sprite-icons.scss';
@import '../sass/_sprite-menu.scss';
@import '../sass/_mixins&includes.scss';

/* ALL STYLES */
// %sprite {
//   background-image: url(../images/sprite-icons.png);
//   background-repeat: no-repeat;
// }

%button-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

/* transition */

.transition-all,
a,
[class^="btn-"],
[class*=" btn-"],
.wrap-input {
  transition: all 0.2s ease 0s;
  // @include sprite($icon-search-white);

}


/* transition end */


/* clearfix */

.clearfix,
.buttons,
.bxslider,
[class*="-columns"],
form {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}


/* clearfix end */


/* reset-list */

[class$="-list"],
[class*="-list "],
[class$="-list"] > li,
[class*="-list "] > li {
  background: none;
  border: 0;
  list-style: none;
  margin: 0;
  padding: 0;

}



/* reset-list end */


/* inline block */

.inline-b,
[class^="btn-"],
[class*=" btn-"],
[class^="icon-"],
[class*=" icon-"] {
  vertical-align: top;
  display: -moz-inline-stack;
  display: inline-block;
  position: relative;
}

.inline-m {
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: middle;
}

/* inline block end */


/* icons */

[class^="icon-"],
[class*=" icon-"] {
  vertical-align: middle;
}

.icon-menu {
  @include sprite($icon-menu-black);
}

.icon-catalog {
  @include sprite($icon-catalog-black);
}

.icon-about {
  @include sprite($icon-about-black);
}


.icon-company {
  @include sprite($icon-company-black);
}

.icon-news {
  @include sprite($icon-news-black);
}

.icon-recomendation {
  @include sprite($icon-recom-black);
}

.icon-help {
  @include sprite($icon-help-black);
}

.icon-del {

  cursor: pointer;
  width: 34px;
  height: 34px;
  position: relative;
  background: #FFF;
  &:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    @include sprite($icon-close-black-sm);
    margin: -6px 0 0 -6px;
  }
  &:hover {
    background: $light_red_color;
    &:before {
      @include sprite($icon-close-white-sm)
    }
  }
  &:focus {
    background: $dark_red_color;
    &:before {
      @include sprite($icon-close-white-sm)
    }
  }
}


/* icons end */


/* clear-list */

.clear-list {
  & li:before {
    display: none;
  }
}
/* clear-list end */

/* columns */
.columns {
  @extend .clearfix;
}
.col-50 {
  width: 50%;
  float: left;
}
.col-33 {
  width: 33.33%;
  float: left;
}
.col-20 {
  width: 20%;
  float: left;
}
.col-25 {
  float: left;
  width: 25%;
}
/* columns end */

/* other reusable styles */

.nowrap {
  white-space: nowrap;
}

ol {
  counter-reset: headings;
}

ol{
  margin: 0px 0;
  padding: 0;
  list-style: none;
  li {
    position: relative;
    &:before {
      position: absolute;
    }
  }
}

