.pagination {
  font-size: 0;
  line-height: 1;
  text-align: center;

  .pagination-item {
    &:first-child {
      margin-left: 66px;

    }
    &:nth-child(2) {
      a {
        border-left: 1px solid #e0e0e0;
      }
    }

    display: inline-block;
    vertical-align: top;

    &.prev,
    &.next {
      .pagination-link {
        text-indent: -9999px;
        overflow: hidden;
        border-color: transparent;
        position: relative;

        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    &.prev {
      margin-right: 16px;

      .pagination-link {
        &:after {
          @include sprite($icon-arrow-prev-black);
        }

        &:hover:after {
          @include sprite-position($icon-arrow-prev-white);
        }
      }
    }
    &.next {
      margin-left: 16px;

      .pagination-link {
        &:after {
          @include sprite($icon-arrow-next-black);
        }

        &:hover:after {
          @include sprite-position($icon-arrow-next-white);
        }
      }
    }

    &.active {
      .pagination-link {
        color: #fff;
        background: $red_color;
        border-color: $red_color;
      }
    }
  }

  .pagination-link {
    display: block;
    min-width: 52px;
    border: 1px solid #e0e0e0;
    border-left: none;
    font: 18px/50px $helvetica-l;
    color: #000;

    &:hover {
      text-decoration: none;
      color: #fff;
      background: $light_red_color;
      border-color: $light_red_color;
    }

    &:active {
      background: $dark_red_color;
      border-color: $dark_red_color;
    }
  }
}

