.post {
  font: 15px/23px $helvetica-l;
  color: #000;
	margin-bottom: 42px;

  .main-ttl {
    padding-bottom: 44px;
  }

  h2 {
    font: 18px/1.2 $helvetica-b;
    text-transform: none;
    padding-bottom: 12px;
  }

  .inner {
    padding-right: 180px;
    padding-left: 183px;
  }

  .img-wrap {
    margin-bottom: 42px;
    text-align: center;

    img {
      max-width: 100%;
      vertical-align: top;
    }
  }

  .colored-red {
    color: $red_color;
  }

