    font: inherit;
    padding-bottom: 23px;
  }

  strong {
    font-family: $helvetica-b;
  }

  em {
    font-family: $helvetica-l;
    font-style: italic;
  }

  ol {
    counter-reset: section1;
    margin-bottom: 23px;

    > li {
      padding-left: 35px;
      position: relative;

      &:before {
        content: counter(section1) ". ";
        counter-increment: section1;
        font-family: $helvetica-b;
        position: absolute;
        left: 0;
        top: 0;
      }
