.custom-gallery {
	.pages {
		margin-bottom: 20px;
		text-align: center;
		font-size: 0;
	}

	.pages-nums {
		display: inline-block;
		vertical-align: middle;
		margin: 0 38px;
		font: 15px/1 $helvetica-l;
		color: #000;
	}

	.pages-prev,
	.pages-next {
		@extend %button-reset;
		display: inline-block;
		width: 34px;
		height: 34px;
		vertical-align: middle;
		text-indent: -9999px;
		overflow: hidden;
		position: relative;
    transition: all 0.5s ease 0s;

		&:hover {
			background: $light_red_color;
		}

		&:active {
			background: $dark_red_color;
		}

		&:after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	.pages-prev {
		&:after {
			@include sprite($icon-arrow-prev-black-bigger);
		}

		&:hover {
			&:after {
				@include sprite-position($icon-arrow-prev-white-bigger);
			}
		}
	}

	.pages-next {
		&:after {
			@include sprite($icon-arrow-next-black-bigger);
		}

		&:hover {
			&:after {
				@include sprite-position($icon-arrow-next-white-bigger);
			}
		}
	}

	.pages-cur {
		margin-right: 4px;
	}

	.pages-total {
		margin-left: 4px;
	}

	.images {
		position: relative;
	}

	.images-item {
		width: 100%;
		opacity: 0;
		text-align: center;
		visibility: hidden;
		transition: visibility 0s .3s, opacity .3s;
		position: absolute;
		left: 0;
		top: 0;

		&.active {
			position: relative;
			opacity: 1;
			visibility: visible;
			transition-delay: 0s;
		}

		img {
			max-width: 100%;
			vertical-align: top;
		}
	}
}

.back-btn {
	display: inline-block;
	padding: 5px 12px 6px 40px;
	font: 15px/22px $helvetica-l;
	color: #000;
	border-radius: 3px;
	position: relative;

	&:hover {
		text-decoration: none;
		color: #fff;
		background: $light_red_color;

		&:before {
			@include sprite-position($icon-arrow-back-white);
		}
	}

	&:active {
		background: $dark_red_color;
	}

	&:before {
		content: "";
		position: absolute;
		left: 12px;
		top: 50%;
		transform: translateY(-50%);
		@include sprite($icon-arrow-back-black);
	}
}

.back-btn-wrap {
	margin-bottom: 16px;
	margin-left: -10px;
	overflow: hidden;

	.back-btn {
		float: left;
	}
}

.news-paging {
	font: 15px/22px $helvetica-l;
	color: #000;
	margin-bottom: 25px;
	@extend .clearfix;

	// left
	li {
    transition: all 0.5s ease 0s;
		&:first-child {
			float: left;
			text-align: left;

			a {
				padding: 0 0 0 30px;

				&:hover {
					&:after {
						@include sprite-position($icon-arrow-back-orange);
					}
				}

				&:active {
					&:after {
						@include sprite-position($icon-arrow-back-dark-orange);
					}
				}

				&:after {
					right: auto;
					left: 0;
					@include sprite-position($icon-arrow-back-black);
				}
			}
		}

		// right
		float: right;
		width: 365px;
		text-align: right;
	}

	a {
		display: block;
		padding: 0 30px 0 0;
		color: inherit;
		position: relative;
		transition: none;
    transition: all 0.5s ease 0s;

		&:hover {
			color: #f15d4a;
			text-decoration: none;

			&:after {
				@include sprite-position($icon-arrow-next-orange);
			}
		}

		&:active {
			color: #ac2817;

			&:after {
				@include sprite-position($icon-arrow-next-dark-orange);
			}
		}

		&:after {
			content: "";
			position: absolute;
			right: 0;
			top: 4px;
			@include sprite($icon-next-arrow-black);
		}
	}
}

