@keyframes scale-in {
  0% {
    transform: scale(1) translateX(0);
  }

  100% {
    transform: scale(1.5) translate(70px, 50px);
  }
}

@keyframes scale-out {
  0% {
    transform: scale(1.5) translate(-70px, 50px);
  }

  100% {
    transform: scale(1) translate(0, 0);
  }
}

.main-slider-wrap {
  position: relative;
  margin: 0 0 20px;

  .bx-viewport {
    min-height: 490px;
    overflow: hidden;
  }
  .bx-controls {
    position: absolute;
    z-index: 100;
    bottom: 30px;
    text-align: center;
    width: 100%;
  }
  .bx-pager {
    margin: 0 0 0 -35px;
  }
  .bx-pager-item {
    display: inline-block;
    margin: 0 0 0 35px;
    a {
      width: 17px;
      height: 17px;
      display: block;
      text-indent: -99999px;
      border-radius: 50%;
      border: 2px solid #fff;
      &:hover {
        border-color: $light_red_color;
        background: $light_red_color;
      }
      &:active {
        border-color: $dark_red_color;
        background: $dark_red_color;
      }
      &.active {
        border-color: $red_color;
        background: $red_color;
      }
    }
  }
  li {
    &:first-child {
      display: block;
    }

    position: relative;
    overflow: hidden;
    display: none;

    &.active-slide {
      &.scale-in {
        img {
          animation: scale-in 6s linear infinite alternate;
        }
      }

      &.scale-out {
        img {
          animation: scale-out 6s linear infinite alternate;
        }
      }
    }

    // increase image size before active class added
    &.scale-out {
      img {
        transform: scale(1.5) translate(-70px, 50px);
      }
    }

    img {
      display: block;
    }
    .overlay {
      background: rgba(0,0,0, 0.30);
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      display: block;
    }
    .text-holder {
      position: absolute;
      top: 29%;
      text-align: center;
      width: 100%;
      h2 {
        color: $main_bg;
        font-family: $helvetica-l;
        font-size: $lg_fs;
        line-height: 34px;
        padding: 0;
        font-weight: normal;
        text-transform: none;
        &:first-letter {
          text-transform: uppercase;
        }
      }
    }
    .slider-logo {
      background: url('../img/logo-white.png') no-repeat;
      display: block;
      width: 102px;
      height: 52px;
      margin: 0 auto 21px;
    }
  }
}
