@font-face {
    font-family: 'helveticaneuecyr';
    src: url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.eot');
    src: url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.woff2') format('woff2'),
         url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.woff') format('woff'),
         url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.ttf') format('truetype'),
         url('../fonts/helvetica/medium/helveticaneuecyr-medium-webfont.svg#helveticaneuecyrmedium') format('svg');
    font-weight: normal;
    font-style: normal;

}

